
<template>
  <div class="card strpied-tabled-with-hover">
    <el-table
      :data="dataSouce.slice((filter.PageIndex-1)*filter.PageSize,filter.PageIndex*filter.PageSize)"
      border
      class="table-hover table-striped"
    >
      <el-table-column prop="編號" label="編號" :formatter="formatter"></el-table-column>
      <el-table-column prop="車隊名稱" label="車隊名稱" :formatter="formatter"></el-table-column>
      <el-table-column prop="所屬名稱" label="所屬名稱" :formatter="formatter"></el-table-column>
      <el-table-column prop="公司地址" label="公司地址" :formatter="formatter"></el-table-column>
      <el-table-column prop="叫車電話" label="叫車電話" :formatter="formatter"></el-table-column>
      <el-table-column prop="申訴電話" label="申訴電話" :formatter="formatter"></el-table-column>
      <el-table-column prop="通話費率" label="通話費率" :formatter="formatter"></el-table-column>
      <el-table-column prop="叫車收費方式" label="叫車收費方式" :formatter="formatter"></el-table-column>
    </el-table>
    <pageination
      :pageIndex="filter.PageIndex"
      :pageSize="filter.PageSize"
      :total="filter.recordCount"
      @handleChange="handleChange"
    ></pageination>
  </div>
</template>

<script>
import TaxiInfoAPIService from 'src/service/TaxiInfoAPIService.js'
import TaxiInfoData from 'src/Data/TaxiInfoData.json'
export default {
  data() {
    return {
      dataSouce: [],
      filter: {
        PageIndex: 1,
        PageSize: 5,
        recordCount: 0
      }
    }
  },
  mounted() {
    this.dataBind()
  },
  methods: {
    dataBind() {
      TaxiInfoAPIService.get()
        .then(response => {
          this.dataSouce = response.data
          this.filter.recordCount = response.data.length
        })
        .catch(response => {
          console.log(response)
          this.dataSouce = TaxiInfoData
          this.filter.recordCount = TaxiInfoData.length
        })
    },
    formatter(row, column, cellValue, index) {
      return cellValue
    },
    handleChange(params) {
      switch (params.proto) {
        case 'Size':
          this.filter.PageSize = params.value
          break
        case 'Current':
          this.filter.PageIndex = params.value
          break
      }
    }
  }
}
</script>