<template>
  <div class="col-md-12">
    <div class="form-group">
      <textarea
        type="text"
        class="form-control border-input"
        id="parameter"
        ref="parameter"
        :value="value"
        @input="$emit('input', $event.target.value)"
        rows="10"
      ></textarea>
    </div>
    <div class="row">
      <div class="col-md-12">
        <div class="table-responsive-md">
          <table class="table table-bordered table-striped">
            <thead>
              <tr>
                <th scope="col">#</th>
                <th scope="col">Key</th>
                <th scope="col">Value</th>
                <th scope="col">Comment</th>
                <th scope="col">Set</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(item, index) in parameterDataSource" :key="index">
                <td scope="row" data-th="index">
                  <label>{{index+1}}</label>
                </td>
                <td data-th="Key">
                  <input type="text" class="form-control" v-model="parameterDataSource[index].Key" />
                </td>
                <td data-th="Value">
                  <input
                    type="text"
                    class="form-control"
                    v-model="parameterDataSource[index].Value"
                  />
                </td>
                <td data-th="Comment">
                  <input
                    type="text"
                    class="form-control"
                    v-model="parameterDataSource[index].Comment"
                  />
                </td>
                <td data-th="Setting">
                  <button
                    class="btn btn-fill btn-sm btn-danger"
                    @click.prevent.stop="removeParameter(index)"
                  >刪除</button>
                </td>
              </tr>
            </tbody>
          </table>
          <button class="btn btn-fill btn-sm btn-primary" @click.prevent.stop="addParameter()">add</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import utils from '@/cores/utils'
export default {
  name: 'serviceParameter',
  props: {
    value: {
      type: String,
      default: '',
      required: true
    }
  },
  created() {},
  mounted() {},
  data() {
    return {
      parameterDataSource: [] // 參數資料來源
    }
  },
  watch: {
    value(newValue, oldValue) {
      if (newValue !== oldValue && this.value !== null) {
        // console.log(utils)
        if (utils.isJSON(this.value.trim())) {
          let parameter = JSON.parse(this.value.trim())
          this.parameterDataSource = []
          for (let item in parameter) {
            this.parameterDataSource.push({
              Key: item,
              Value: parameter[item].value,
              Comment: parameter[item].comment
            })
          }
        } else if (this.value === null || this.value === '') {
          this.parameterDataSource = []
        }
      } else if (this.value === null) {
        this.parameterDataSource = []
      }
    },
    parameterDataSource: {
      handler(newValue, oldValue) {
        let parameter = {}
        for (let item in this.parameterDataSource) {
          parameter[this.parameterDataSource[item].Key] = {}
          parameter[
            this.parameterDataSource[item].Key
          ].value = this.parameterDataSource[item].Value
          parameter[
            this.parameterDataSource[item].Key
          ].comment = this.parameterDataSource[item].Comment
        }

        this.$emit('input', JSON.stringify(parameter))
      },
      deep: true
    }
  },
  computed: {},
  mounted() {},
  activated() {},
  methods: {
    addParameter() {
      this.parameterDataSource.push({
        Key: '',
        Value: '',
        Comment: ''
      })
    },
    removeParameter(index) {
      this.parameterDataSource.splice(index, 1)
    }
  }
}
</script>

<style lang="scss" type="text/scss">
@import './JsonformatParameter.scss';
</style>