<template>
  <div class="card strpied-tabled-with-hover">
    <el-table
      :data="dataSouce.slice((filter.PageIndex-1)*filter.PageSize,filter.PageIndex*filter.PageSize)"
      border
      class="table-hover table-striped"
    >
      <el-table-column prop="捷運站代號" label="捷運站代號" :formatter="formatter"></el-table-column>
      <el-table-column prop="站名" label="站名" :formatter="formatter"></el-table-column>
      <el-table-column prop="經度" label="經度" :formatter="formatter"></el-table-column>
      <el-table-column prop="緯度" label="緯度" :formatter="formatter"></el-table-column>
    </el-table>
    <pageination
      :pageIndex="filter.PageIndex"
      :pageSize="filter.PageSize"
      :total="filter.recordCount"
      @handleChange="handleChange"
    ></pageination>
  </div>
</template>
<script>
import MRTInfoAPIService from 'src/service/MRTInfoAPIService.js'
import MRTInfoData from 'src/Data/MRTInfoData.json'
export default {
  data() {
    return {
      dataSouce: [],
      filter: {
        PageIndex: 1,
        PageSize: 5,
        recordCount: 0
      }
    }
  },
  mounted() {
    this.dataBind()
  },
  methods: {
    dataBind() {
      MRTInfoAPIService.get()
        .then(response => {
          this.dataSouce = response.data
          this.filter.recordCount = response.data.length
        })
        .catch(response => {
          console.log(response)
          this.dataSouce = MRTInfoData
          this.filter.recordCount = MRTInfoData.length
        })
    },
    formatter(row, column, cellValue, index) {
      return cellValue
    },
    handleChange(params) {
      switch (params.proto) {
        case 'Size':
          this.filter.PageSize = params.value
          break
        case 'Current':
          this.filter.PageIndex = params.value
          break
      }
    }
  }
}
</script>