import DashboardLayout from '../layout/DashboardLayout.vue'
// GeneralViews
import NotFound from 'src/pages/Error/Page404.vue'

// Admin pages
import UserProfile from 'src/pages/UserProfile.vue'
import APIProfile from 'src/pages/APIProfile.vue'
import ElementIcons from 'src/pages/Element/ElementIcons.vue'
import EmploymentHistory from 'src/pages/EmploymentHistory.vue'
import Jsonformat from 'src/pages/Jsonformat.vue'
const routes = [
  {
    path: '/',
    component: DashboardLayout,
    redirect: '/user'
  },
  {
    path: '/',
    component: DashboardLayout,
    redirect: '/User',
    children: [
      {
        path: 'user',
        name: 'User',
        component: UserProfile
      },
      {
        path: 'EmploymentHistory',
        name: 'Employment History',
        component:EmploymentHistory
      },
      {
        path: 'APIProfile',
        name: 'APIProfile',
        component:APIProfile
      },
      {
        path: 'ElementIcons',
        name: 'Element Icons',
        component:ElementIcons
      },
      {
        path: 'Jsonformat',
        name: 'Jsonformat',
        component:Jsonformat
      },
      { path: '*', component: NotFound }
    ]
  }
]

/**
 * Asynchronously load view (Webpack Lazy loading compatible)
 * The specified component must be inside the Views folder
 * @param  {string} name  the filename (basename) of the view to load.
function view(name) {
   var res= require('../components/Dashboard/Views/' + name + '.vue');
   return res;
};**/

export default routes
